.wheel{
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    transform :translate(-50%);
    animation:ani-mouse 2s linear infinite;
}
@media screen and (max-width:1024px) {
    .hone__social-link{
        font-size: 1.125rem;
    }
}
